<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="nucleicAcidID">
            <Input v-model="editForm.nucleicAcidID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="libID">
            <Input v-model="editForm.libID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.collector" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <DatePicker v-model="editForm.collectDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.useRange" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.useRangeUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.measure" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.measureUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.density" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.densityUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.total" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.totalUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.insert" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.insertUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Select v-model="editForm.quality" placeholder="" style="width: 280px">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
          </FormItem>
          <FormItem label="">
            <Select v-model="editForm.status" placeholder="" style="width: 280px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestUpdateDate:'111',
          nucleicAcidID:'111',
          libID:'111',
          collector:'111',
          collectDate:'2018-01-01',
          useRange:'111',
          useRangeUnit:'111',
          measure:'111',
          measureUnit:'111',
          density:'111',
          densityUnit:'111',
          total:'111',
          totalUnit:'111',
          insert:'111',
          insertUnit:'111',
          quality:'',
          status:''
        },
        rule:{
          nucleicAcidID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          libID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/library')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


