<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="" prop="nucleicAcidID">
            <Input v-model="addForm.nucleicAcidID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="libID">
            <Input v-model="addForm.libID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.collector" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <DatePicker type="daterange" v-model="addForm.collectDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.useRange" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.useRangeUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.measure" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.measureUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.density" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.densityUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.total" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.totalUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.insert" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.insertUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Select v-model="addForm.quality" placeholder="" style="width: 280px">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
          </FormItem>
          <FormItem label="">
            <Select v-model="addForm.status" placeholder="" style="width: 280px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          nucleicAcidID:'',
          libID:'',
          collector:'',
          collectDate:'',
          useRange:'',
          useRangeUnit:'',
          measure:'',
          measureUnit:'',
          density:'',
          densityUnit:'',
          total:'',
          totalUnit:'',
          insert:'',
          insertUnit:'',
          quality:'',
          status:''
        },
        rule:{
          nucleicAcidID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          libID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
              this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/library')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


