<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary"></BreadcrumbItem>
      <BreadcrumbItem>QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>QC</p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p>QC</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="" prop="libID">
            <Input v-model="addForm.libID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="totalbase">
            <Input v-model="addForm.totalbase" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.totalSequence" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.repetitionRate" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.compareRate" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.sequenceLength" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.captureEfficiency" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.probeAverage" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.probeMiddele" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.probeHomogeneity" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.coverRate" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="30">
            <Input v-model="addForm.coverRate30" placeholder="30" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="addForm.status" placeholder="" style="width: 280px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          libID:'',
          totalbase:'',
          totalSequence:'',
          repetitionRate:'',
          compareRate:'',
          sequenceLength:'',
          captureEfficiency:'',
          probeAverage:'',
          probeMiddele:'',
          probeHomogeneity:'',
          coverRate:'',
          coverRate30:'',
          status:''
        },
        rule:{
          libID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          totalbase:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
              this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


