<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="" prop="libID">
            <Input v-model="addForm.libID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ChrName">
            <Input v-model="addForm.ChrName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="Position">
            <Input v-model="addForm.Position" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="addForm.Symbol" placeholder="Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.transcript" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF">
            <Input v-model="addForm.ref" placeholder="REF" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT">
            <Input v-model="addForm.alt" placeholder="ALT" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.style" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.genotype" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.geneFrequency" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.genotype" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.geneFrequency" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="addForm.exon" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="addForm.exonPosition" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="addForm.hgvsp" placeholder="HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="addForm.hgvsc" placeholder="HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="addForm.rsID" placeholder="rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.clini" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.sign" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          libID:'111',
          ChrName:'111',
          Position:'111',
          Symbol:'111',
          transcript:'111',
          ref:'111',
          alt:'111',
          style:'111',
          genotype:'111',
          geneFrequency:'111',
          exon:'111',
          exonPosition:'111',
          hgvsp:'111',
          hgvsc:'111',
          rsID:'111',
          clini:'111',
          sign:'111'
        },
        rule:{
          libID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          ChrName:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          Position:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          Symbol:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
              this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


