<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/moldiagnosis"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="" prop="SubjectID">
            <Input v-model="editForm.SubjectID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.SubjectName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.checkDate" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.doctor" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.organization" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <div>
            <h2></h2>
            <FormItem label="Panel">
              <Input v-model="editForm.PanelName" placeholder="Panel" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Product">
              <Input v-model="editForm.ProductName" placeholder="Product" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Pipeline">
              <Input v-model="editForm.PipelineName" placeholder="Pipeline" style="width: 280px"></Input>
            </FormItem>
          </div>
          <FormItem label="">
            <Input v-model="editForm.status" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.reportor" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.reviewer" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.reportDate" placeholder="" style="width: 280px"></Input>
          </FormItem>     
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')"></Button>
            <Button @click="handleSubmit('editForm')"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        chrID:location.search.split("=")[1],
        editForm:{
          SubjectID:'111',
          SubjectName:'',
          checkDate:'',
          doctor:'',
          organization:'',
          PanelName:'',
          ProductName:'',
          PipelineName:'',
          status:'',
          reportor:'',
          reviewer:'',
          reportDate:''
        },
        rule:{
          SubjectID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$router.push('/moldiagnosis')
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


