<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/biospeciemens"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="" prop="molDiagnosisID">
            <Input v-model="addForm.molDiagnosisID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="pathologyID">
            <Input v-model="addForm.pathologyID" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="addForm.bioStyle" placeholder="" style="width: 280px">
              <Option value="1111">1111</Option>
              <Option value="2222">2222</Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.samplingSite" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <DatePicker type="daterange" v-model="addForm.samplingDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="">
            <DatePicker type="daterange" v-model="addForm.collectionDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.Sampler" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.isCancerCell" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.cancerCellPrecent" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.status" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.sampleMount" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.sampleMountUnit" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          molDiagnosisID:'',
          pathologyID:'',
          bioStyle:'',
          samplingSite:'',
          samplingDate:'',
          collectionDate:'',
          Sampler:'',
          isCancerCell:'',
          cancerCellPrecent:'',
          status:'',
          sampleMount:'',
          sampleMountUnit:'',
          
        },
        rule:{
          molDiagnosisID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          pathologyID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/biospeciemens")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


