<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary"></BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='browvariant'">QC</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='editvariant'">QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="libID">
            <Input v-model="editForm.libID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ChrName">
            <Input v-model="editForm.ChrName" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="Position">
            <Input v-model="editForm.Position" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="editForm.Symbol" :disabled="currentPath==='browvariant'" placeholder="Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.transcript" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF">
            <Input v-model="editForm.ref" :disabled="currentPath==='browvariant'" placeholder="REF" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT">
            <Input v-model="editForm.alt" :disabled="currentPath==='browvariant'" placeholder="ALT" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.style" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.genotype" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.geneFrequency" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.genotype" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.geneFrequency" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="editForm.exon" :disabled="currentPath==='browvariant'" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="editForm.exonPosition" :disabled="currentPath==='browvariant'" placeholder="exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="editForm.hgvsp" :disabled="currentPath==='browvariant'" placeholder="HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="editForm.hgvsc" :disabled="currentPath==='browvariant'" placeholder="HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="editForm.rsID" :disabled="currentPath==='browvariant'" placeholder="rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.clini" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.sign" :disabled="currentPath==='browvariant'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit" v-if="currentPath=='browvariant'"></Button>
              <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath=='editvariant'"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[1],
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestUpdateDate:'111',
          libID:'111',
          ChrName:'111',
          Position:'111',
          Symbol:'111',
          transcript:'111',
          ref:'111',
          alt:'111',
          style:'111',
          genotype:'111',
          geneFrequency:'111',
          exon:'111',
          exonPosition:'111',
          hgvsp:'111',
          hgvsc:'111',
          rsID:'111',
          clini:'111',
          sign:'111'
        },
        rule:{
          libID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          ChrName:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          Position:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          Symbol:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="editvariant"
        this.$router.push("/editvariant")
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


