<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library"></BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary"></BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='browqc'">QC</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='editqc'">QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="">
            <Input v-model="editForm.CreateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="libID">
            <Input v-model="editForm.libID" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="totalbase">
            <Input v-model="editForm.totalbase" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.totalSequence" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.repetitionRate" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.compareRate" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.sequenceLength" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.captureEfficiency" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editForm.probeAverage" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.probeMiddele" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.probeHomogeneity" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editForm.coverRate" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="30">
            <Input v-model="editForm.coverRate30" :disabled="currentPath=='browqc'" placeholder="30" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="editForm.status" :disabled="currentPath=='browqc'" placeholder="" style="width: 280px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit" v-if="currentPath=='browqc'"></Button>
              <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath=='editqc'"></Button>
              <Button @click="backList"></Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[1],
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestUpdateDate:'111',
          libID:'111',
          totalbase:'111',
          totalSequence:'111',
          repetitionRate:'111',
          compareRate:'111',
          sequenceLength:'111',
          captureEfficiency:'111',
          probeAverage:'111',
          probeMiddele:'111',
          probeHomogeneity:'111',
          coverRate:'111',
          coverRate30:'111',
          status:''
        },
        rule:{
          libID:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ],
          totalbase:[
            {
              required: true, message: '', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="editqc"
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


